import React from "react";
import {useState, useEffect} from "react";
import {doc, getDocs, collection, updateDoc, addDoc, deleteDoc} from "firebase/firestore";
import {db} from './firebase-Config';
const App = () =>{
  const [name, setName] = useState("");
  const [price, setPrice] = useState(0);
  const [data, setData] = useState([]);
  const [showData, setShowData] = useState([]);
  let total = 0;
  const collectionsRef = collection(db, "todo");
  const addData = async()=>{
    await addDoc(collectionsRef, {name: name, price: Number(price)});
    setName("");
    setPrice("");
  }
  const handleSubmit = (e) =>{
    e.preventDefault();
    if(name && price){
      setData({name, price, id: new Date().getTime().toString()});
      addData();
      setName("");
      setPrice(0);
      return;
      
    }
    else{
window.alert("Empty field");
return;
    }
  }
  
  
    useEffect(()=>{
      const fetchDb = async() =>{
        const data =  await getDocs(collectionsRef);
        setShowData(data.docs.map((doc)=>{
          return {...doc.data(), id: doc.id};
        }))
       }
      fetchDb();
    })
    const deleteItem = async(id)=>{
      const spItem = doc(db,"todo", id);
  await deleteDoc(spItem);
    }
  return (
    <div className="App">
      <h1>Grocery Items</h1>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="name">
            Name:                   
          </label>
        <input type="name" id="name" name="name" onChange={(e)=>setName(e.target.value)}/>
        </div>
        <div>
          <label htmlFor="price">
            Price:
          </label>
        <input type="number" id="price" name="price" onChange={(e)=>setPrice(e.target.value)}/>
        </div>
        <div>
        <button>Add</button>
        </div>
       
      </form>
      <div>
      
        {
          
          showData.map((d)=>{
            total += d.price;
          return(
            
            <div className="items" key={d.id}>
              <h1>{d.name}</h1>
              <h2>{d.price}</h2>
              <button onClick={()=>{deleteItem(d.id)}}>Delete Item</button>
            </div>
          );
        })}
      </div>
        <h1>Total: {total}</h1>
    </div>
  );
}
export default App;